<template>
  <div class="workdesignList">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-card-title>
        工單
        <!-- <v-img :src= "logoImage" aspect-ratio="2.5" contain></v-img> -->
        <v-spacer></v-spacer>
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model = "searchKey"
            v-if="auth.workdesign_search_key"
          ></v-text-field>

          <popupadd ref="editProcess" :workdesignItems="items" @getAllData="getAllData" v-if="auth.workdesign_add_key"></popupadd>
        </v-toolbar>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
          locale="zh-tw"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="text-no-wrap"  >
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <th class="text-left">日期</th>
                    <th class="text-left">客戶統編/名稱</th>
                    <!-- <th class="text-left">單據號碼</th> -->
                    <th class="text-left">創建人員</th>
                    <th class="text-left">修改紀錄</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.items" :key="index">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn icon v-on="on">
                            <img src="@/assets/gear.svg" alt="">
                          </v-btn>
                        </template>
                        <v-list>
                          <v-list-item
                            @click="print(item)"
                          >
                            <v-list-item-title>列印</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="edit(item)"
                            v-if="auth.workdesign_edit_key"
                          >
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="del(item)"
                            v-if="auth.workdesign_del_key"
                          >
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    
                    <td>{{ item.date }}</td>
                    <td>{{ item.customer.text }}</td>
                    <!-- <td>{{ item.code }}</td> -->
                    <td>
                      {{ item.create_man }}
                    </td>
                    <td>
                      {{ item.edit_man }}
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
      <!-- <pre>{{items}}</pre> -->
    </v-card>
  </div>
</template>

<script>
import popupadd from "@/views/main/WorkdesignAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      searchKey: "",
      auth: "", //各畫面授權使用功能限定(載入放在mounted)
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      logoImage:require('@/assets/img/job_3d_cad_designer.png'),
      picBaseUrl: this.$store.state.base_url + "/upload/workdesign/",
      selectedFile:"",
      uploadItem:"",
    };
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  computed: {
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";
   
      return  keys.reduce(function(prev, element) {
        return prev.filter(item => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
      
    }
  },

  methods: {
    getAllData() {
      //取得所有資料
      this.myAxios("general/getAll/" + this.$store.state.databaseName + "/workdesign")
        .then((rs) => {
          this.items = rs.map((i) => {
            let newItem = JSON.parse(i.datalist);
            newItem.snkey = i.snkey;
            if (newItem.picname == undefined){
              newItem.picname = 'lazypic.jpg';
            }
            return newItem;
          });
        });
    },
    //修改功能
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    //刪除功能
    del(item) {
      //yn() 全域函數寫在main.js裡
      this.yn().then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          item.delman = this.$store.state.pData.name + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")";

          var postData = {
            snkey: item.snkey,
            tablename: "workdesign",
            datalist: JSON.stringify(item),
          };

          let url = "general/delv3/" + this.$store.state.databaseName + "/" + postData.tablename;
          
          this.myAxios(url,postData).then((rs) => {
              if (rs.state == 1) {
                this.$swal({ icon: "success", text: "已刪除!" });
                this.getAllData();
              }
          });

          //判斷是否存在圖片 -> 存在就刪掉
          if (item.picname != 'lazypic.jpg'){
            console.log('delpic')
            url = "general/delPic/" + this.$store.state.databaseName + "/" + postData.tablename + "/" + item.picname;
            this.myAxios(url,postData).then((rs) => { console.log(rs)});
          }
        }
      });
      
    },
    //列印功能
    print(item){
      let routeData = this.$router.resolve({path: "/print/workdesignprint",query:{data:JSON.stringify(item)}});
      window.open(routeData.href, "_blank");
    },
    
  },
};
</script>


<style lang="scss">
.workdesignList {
  .theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr > th{
    font-size: 1rem;
  }
}
</style>