<template>
  <div class="personneladd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor1">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" sm="4">
                <v-text-field
                  label="品名"
                  prepend-icon="mdi-material-design"
                  v-model="list.name"
                  v-if="dialog"
                  autofocus
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4">
                <v-text-field
                  label="厚度"
                  prepend-icon="mdi-current-dc"
                  v-model="list.thickness"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>

              <v-col cols="12" sm="4">
                <v-text-field
                  label="價格"
                  prepend-icon="mdi-cash-usd"
                  v-model="list.price"
                ></v-text-field>
              </v-col>

              <v-col cols="12" sm="4">
                <v-text-field
                  label="特殊比重"
                  prepend-icon="mdi-cash-usd"
                  v-model="list.proportion"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4" v-if="processType == 'edit'">
                <v-text-field
                  label="庫存"
                  prepend-icon="mdi-cash-usd"
                  v-model="list.store"
                ></v-text-field>
              </v-col>

              <v-col cols="12">
                <v-autocomplete
                  label="設定適用廠商"
                  prepend-icon="mdi-office-building"
                  v-model="list.customers"
                  :items="customerItems"
                  counter
                  chips
                  clearable
                  deletable-chips
                  multiple
                  return-object
                >
                </v-autocomplete>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType == 'add'"
            >確認新增</v-btn
          >
          <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
            >確認修改</v-btn
          >
        </v-card-actions>
        <!-- <pre>{{ list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  props: ["items"],
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
    };
  },

  computed: {
    customerItems() {
      return this.$store.state.customerItems.map((i) => {
        return {
          text: i.name,
          snkey: i.snkey,
        };
      });
    },
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
    this.$store.dispatch("getItems", "customer");
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增品名";
      this.titleStyle = "font-weight-black error lighten-2";
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改品名資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        //判斷名稱重覆
        let repeatMatch = this.items.find((i) => {
          return (
            i.name == this.list.name &&
            i.thickness == this.list.thickness &&
            i.price == this.list.price
          );
        });

        if (repeatMatch) {
          this.$swal({ icon: "warning", text: "名稱,厚度及價格重覆!!" });
          return false;
        }
        //判斷重覆

        this.list.personnel_snkey = this.$store.state.pData.snkey;
        this.list.create_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")"; //執行動作的人,時間,內容
        let postData = {
          personnel_snkey: this.list.personnel_snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        this.myAxios(
          "general/add/" + this.$store.state.databaseName + "/rawmaterial",
          postData
        )
          .then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已新增" });
              // this.$emit("getAllData");
              this.$store.dispatch("getItems", "rawmaterial");
            }
          })
          .catch((err) => {
            this.$swal({ icon: "err", text: "資料存取錯誤!!內容:" + err });
          });
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man; //執行動作的人,時間,內容
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        this.myAxios(
          "general/edit/" + this.$store.state.databaseName + "/rawmaterial",
          postData
        ).then((rs) => {
          if (rs.state == 1) {
            this.$swal({
              icon: "success",
              text: "已修改",
            });
            this.$store.dispatch("getItems", "rawmaterial");
          }
        });
      }
    },
  },
};
</script>

