<template>
  <div>
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-img
          :src="picBaseUrl + item.picname"
          :lazy-src="picBaseUrl + 'lazypic.jpg'"
          aspect-ratio="1"
          class="d-flex align-end"
          width="80px"
          v-on="on"
        >
        </v-img>
      </template>

      <v-card :color="$store.state.colorSet.bgColor2">
        <v-card-title></v-card-title>  
        <v-card-text>
          <v-img
            class="mt-1"
            :src="picBaseUrl + item.picname"
            :lazy-src="picBaseUrl + 'lazypic.jpg'"
            aspect-ratio="1"
            width="100%"
            @click.stop="dialog = !dialog"
          >
          </v-img>
        </v-card-text>

        <v-btn fixed top icon large class="mt-3" @click.stop="dialog = !dialog">
          <v-icon class="text--white">mdi-close</v-icon>
        </v-btn>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  props: ["item", "picBaseUrl"],
  data: () => ({
    dialog: false,
  }),
};
</script>