<template>
  <div class="MachiningList">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="3" class="d-flex">
            <span>加工管理</span>
            <!-- <v-img class="d-none d-sm-flex" :src="logoImage" aspect-ratio="2.5" contain></v-img> -->
          </v-col>
          
          <v-col cols="12" md="9">
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="searchKey"
                v-if="auth.machining_search_key"
              ></v-text-field>

              <!-- 上傳file用的input -->
              <input
                type="file"
                accept="image/*"
                ref="fileInput"
                @change="onFileSelected"
                style="display: none"
              />

              <popupadd ref="editProcess" @getAllData="getAllData" v-if="auth.machining_add_key"></popupadd>
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
          locale="zh-tw"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="text-no-wrap"  >
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <!-- <th class="text-left">pictest</th> -->
                    <th class="text-left">圖樣</th>
                    <th class="text-left">折工名稱</th>
                    <th class="text-left">價格</th>

                    <th class="text-left">創建人員</th>
                    <th class="text-left">修改紀錄</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.items" :key="index">
                    <td class="pa-0">
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn icon v-on="on">
                            <img src="@/assets/gear.svg" alt="">
                          </v-btn>
                        </template>
                        <v-list>
                          <v-list-item
                            @click="edit(item)"
                            v-if="auth.machining_edit_key"
                          >
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="del(item)"
                            v-if="auth.machining_del_key"
                          >
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="uploadItem = item;$refs.fileInput.click()"
                          >
                            <v-list-item-title>上傳圖片</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td class="px-1">
                      <div style="position: relative">
                        <showBigpic :item="item" :picBaseUrl="picBaseUrl" />

                      </div>
                    </td>
                    <td>{{ item.name }}</td>
                    <td>{{ item.price }}</td>
                    <td>
                      {{ item.create_man }}
                    </td>
                    <td>
                      {{ item.edit_man }}
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
      <!-- <pre>{{items}}</pre> -->
    </v-card>
  </div>
</template>

<script>
import showBigpic from "@/components/showBigpic";
import popupadd from "@/views/main/MachiningAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd, showBigpic },
  data() {
    return {
      // items: [], //目前讀取進來的資料庫
      searchKey: "",
      auth: "", //各畫面授權使用功能限定(載入放在mounted)
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      logoImage: require("@/assets/img/machine_robot_arm.png"),
      picBaseUrl: this.$store.state.base_url + "/upload/machining/",
      selectedFile: "",
      uploadItem: "",
    };
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  computed: {
    items(){
      return this.$store.state.machiningItems;
    },
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";

      let rsData = keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);

      rsData = rsData.sort((a, b) => {
        return a[this.sortKey] < b[this.sortKey] ? 1 : -1;
      });

      return rsData;
    },
  },

  methods: {
    //取得所有資料
    getAllData() {
      this.$store.dispatch("getItems",'machining');
    },
    //修改功能
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    //刪除功能
    del(item) {
      //yn() 全域函數寫在main.js裡
      this.yn().then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          item.delman =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";

          var postData = {
            snkey: item.snkey,
            tablename: "machining",
            datalist: JSON.stringify(item),
          };

          let url =
            "general/delv3/" +
            this.$store.state.databaseName +
            "/" +
            postData.tablename;

          this.myAxios(url, postData).then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已刪除!" });
              this.getAllData();
            }
          });

          //判斷是否存在圖片 -> 存在就刪掉
          if (item.picname != "lazypic.jpg") {
            console.log("delpic");
            url =
              "general/delPic/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename +
              "/" +
              item.picname;
            this.myAxios(url, postData).then((rs) => {
              console.log(rs);
            });
          }
        }
      });
    },
    //選取圖片
    onFileSelected(event) {
      this.selectedFile = event.target.files[0];
      var extension = this.selectedFile.name.split(".").pop().toLowerCase(); //取副檔名
      var fitType = ["gif", "png", "jpg", "jpeg"];

      var errorMsg = ""; //判斷檔案是否為圖檔及檔案大小
      if (fitType.indexOf(extension) == -1) {
        errorMsg = "檔案:" + this.selectedFile.name + "不是支持的影像檔案";
      }
      if (this.selectedFile.size > 1024 * 1024 * 5) {
        //檔案大小設定-最大5MB
        errorMsg = "檔案:" + this.selectedFile.name + "不能超過5M唷";
      }

      if (errorMsg == "") {
        this.onUpload();
      } else {
        this.$swal({ icon: "error", text: errorMsg });
        this.selectedFile = "";
      }
    },
    //執行上傳&更新原始資料
    onUpload() {
      //圖片上傳 -> 單張
      const fd = new FormData();
      fd.append("snkey", this.uploadItem.snkey); //放入上傳圖檔檔案的snkey
      fd.append("image", this.selectedFile);

      this.axios
        .post(
          "general/upload/" + this.$store.state.databaseName + "/machining",
          fd
        )
        .then((rs) => {
          console.log(rs.data);
          this.$refs.fileInput.value = "";
          this.items.some((item) => {
            if (item.snkey == this.uploadItem.snkey) {
              item.picname = rs.data.picname;
            }
          });

          //更新原始資料
          let postData = {
            snkey: this.uploadItem.snkey,
            datalist: JSON.stringify(this.uploadItem),
          };

          this.myAxios(
            "general/edit/" + this.$store.state.databaseName + "/machining",
            postData
          ).then((rs) => {
            console.log(rs);
          });
        });
    },
  },
};
</script>


<style lang="scss">
.MachiningList {
  .theme--light.v-data-table
    > .v-data-table__wrapper
    > table
    > thead
    > tr
    > th {
    font-size: 1rem;
  }
}
</style>